import React, { useState, useEffect } from "react";
import {Button} from "react-bootstrap";
import { getCurrentWalletConnected } from "../utils/interact.js";
import Walletmodel from "../utils/walletmodal.js";


import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';

const alchemyKey = process.env.REACT_APP_ALCHEMY_KEY;
const contractABI = require("../contract-abi.json");
const contractAddress = "0x751A9a39AF7E70C37916262644e4D0b8fa018CDE";
const { createAlchemyWeb3 } = require("@alch/alchemy-web3");
const web3 = createAlchemyWeb3(alchemyKey);
const contract = new web3.eth.Contract(contractABI, contractAddress);  

const mintNFT = async(amount, price) => {  
  const {address} = await getCurrentWalletConnected();
  if (address === "") {
    return {
      success: false, 
      status: "â—Please make sure wallet connected.",           
    }
  }  
  else {      
    contract.methods.mint(amount).send({from: address, gasPrice: 80000000000, gas: 160000*amount, value: (amount * price)})
    .on("confirmation", function () {      
    })
    .on('error', async function (error, receipt) {
      console.log(error);      
    }); 

    return {
      status: "",
    }   
    
  }
}    

const Mint = () => {
  const [walletAddress, setWallet] = useState("");  
  const [show, setShow] = useState(false);
  const [tokenNumber, setTokenNumber] = useState(1);
  const [status, setStatus] = useState(""); 
  const publicsale = true;
  const [supply, setSupply] = useState(0);

  const handleClose = () => setShow(false);  
  const publicsale_price = 180000000000000000;

  const handleShow = async () => {  
    setShow(true);   
  };

  const onMintHandle = () => {       
    setStatus("Mint not allowed!");
  };

  const onMintPressed = async () => {          
    const { status } = await mintNFT(tokenNumber, publicsale_price); 
      setStatus(status);         
  };

  useEffect(() => {    
    async function fetchData() {
      const {address} = await getCurrentWalletConnected();      
      contract.methods.totalSupply().call().then((_supply) => {        
        setSupply(_supply);
      }).catch((err) => console.log(err))
      setWallet(address);    
      addWalletListener(); 
    }
    fetchData();
  }, []);

  function addWalletListener() {
    if (window.ethereum) {
      window.ethereum.on("accountsChanged", (accounts) => {
        if (accounts.length > 0) {
          setWallet(accounts[0]);          
        } else {
          setWallet("");          
        }
      });
    } else {
         
    }
  } 

  // const connectWalletPressed = async () => {
  //   const walletResponse = await connectWallet();    
  //   setWallet(walletResponse.address);
  // };
  const {web3Loading, getweb3} = Walletmodel ();
  const [web3, setMyWeb3] = useState ();
  async function connectWallet () {
    await getweb3 ().then((response) => {
      setMyWeb3 (response);      
      response.eth.getAccounts().then ((result) => {
        setWallet(result[0]);
        console.log (web3Loading);        
      });
    });
  };




  return (
    <div className="mint-container purple-text">
      <div className="mint-header text-center text-white pt-5" id="mint">
        Mint
      </div>
      <div className="mint-panel">
        <div className="mint-title pt-4 pt-md-5 text-white">
          Mint Cards
        </div>
        <div className="mint-narration text-white text-center">
          1 NFT PER WALLET AND MINT PRICE 0.18ETH.
        </div>
        <div className="collage mb-5">
          <img src="./images/2.gif" alt="" width="250" height="250"/>
        </div>
        <div className="text-center text-white">
          <Button className="connect-btn mt-3" onClick={connectWallet}>
            {walletAddress.length > 0 ? (                    
              "" +
              String(walletAddress).substring(0, 6) +
              "..." +
              String(walletAddress).substring(38)
              ) : (
                <span>Connect</span>
              )
            } 
          </Button>
        </div>
        <div className="mint-status text-center text-white">
          {supply} / 50 Sold Out 
        </div>
        <div className="text-center">
          <Button className="mint-btn mb-3 mb-md-5" onClick={handleShow}>
            Mint
          </Button>
        </div>
      </div>  
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Mint some Spell Cards</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <img src="./images/2.gif" alt="" style={{maxWidth: "220px"}}/>
          <div className="mint-number">
            {/* <button type="button" onClick={decreaseTokenNumber}><span aria-hidden="true">-</span></button> */}
            <Form>
              <Form.Label>
                {/* { tokenNumber } */}
              </Form.Label>                  
              {status ? <div>{status}</div> 
                : <div></div>}
            </Form>                
            {/* {(tokenNumber < 2) ? <button type="button" onClick={() => setTokenNumber(tokenNumber + 1)}><span aria-hidden="true">+</span></button> 
              : <button type="button" onClick={() => setTokenNumber(tokenNumber)}><span aria-hidden="true">+</span></button>
            } */}
          </div>
        </Modal.Body>
        <Modal.Footer>
          {(!publicsale) ? <Button variant="primary" onClick={onMintHandle}>Mint</Button> 
            : <Button variant="primary" onClick={onMintPressed}>Mint</Button>
          }              
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>               
    </div>
  );  
};

export default Mint;