import React, { useState, useEffect } from "react";
import {
  Navbar,
  Nav 
} from "react-bootstrap";
 
const Navigation = () => { 
  const [sticky, setSticky] = useState(false);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
  }, []);

  const handleScroll = () => {
    if (window.scrollY >= 86) {
      setSticky(true);
    } else if (window.scrollY < 85) {
      setSticky(false);
    }
  }

  return (
    <div className={`header${sticky ? ' sticky' : ''}`}>
      <Navbar variant="dark" expand="md" style={{backgroundColor: '#333333'}}>
        <Navbar.Brand href="/" className="px-4">
          
          <Nav.Link href="#home" className="cus-nav-link text-white" onClick={() => window.scrollTo(0, 0)}><img src="./images/logo.png" className="logo-img" alt=""/></Nav.Link>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" className="mx-4"/>
        <Navbar.Collapse id="basic-navbar-nav" className="d-md-flex justify-content-between">
          <Nav className="mr-auto flex-grow-1">                               
            {/* <Nav.Link href="#home" className="cus-nav-link text-white" onClick={() => window.scrollTo(0, 0)}>Home</Nav.Link> */}
            {/* <Nav.Link href="#animation" className="cus-nav-link">Animation</Nav.Link> */}
            <Nav.Link href="#mint" className="cus-nav-link text-white">Mint</Nav.Link> 
            {/* <Nav.Link href="#roadmap" className="cus-nav-link text-white">Roadmap</Nav.Link> 
            <Nav.Link href="#faq" className="cus-nav-link text-white">FAQs</Nav.Link>         */}
            <div className="d-flex mx-4 mb-2 mb-md-0 social-link">                
              <a href="https://twitter.com/spellcard050" target="_blank" className="d-flex align-items-center pe-3" rel="noreferrer">              
                <img src="https://img.icons8.com/color/48/000000/twitter--v1.png" loading="lazy" width="32" alt=""/>
              </a>             
              <a href="https://discord.gg/F6wgPQeRjn" target="_blank" className="d-flex align-items-center pe-3" rel="noreferrer">              
                <img src="https://img.icons8.com/color/50/000000/discord--v2.png" loading="lazy" width="32" alt=""/>
              </a>  
            </div> 
          </Nav>                    
        </Navbar.Collapse>
      </Navbar>
    </div>
  );
};
export default Navigation;